import "swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "carrousel",
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOption: {
        pagination: {
            el:".swiper-paginations"
        },
        paginationClickable: true,
        autoplay: {
            delay: 3000, //ms
            stopOnLastSlide: false, //slide
            stopOnLastSlide: true, //trueslide
            disableOnInteraction: true, //swiperautoplay
            reverseDirection: true, //
            waitForTransition: true, //slide
        },
        autoplayDisableOnInteraction: false,
        loop: true,
        speed:1000,
        effect: 'cube',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        // coverflow: {
        //   rotate: 50,
        //   stretch: 10,
        //   depth: 100,
        //   modifier: 1,
        //   slideShadows: true
        // },
        //  cubeEffect: {
        //   slideShadows: true, //slide true
        //   shadow: true, // true
        //   shadowOffset: 100, // 20px
        //   shadowScale: 0.6 //0.94
        // },
        coverflowEffect: {
          rotate: 30,
          stretch: 10,
          depth: 60,
          modifier: 2,
          slideShadows : true
        },
      },
      items:[1,2,3]
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {
    // current swiper instance
    // swiper
    console.log("this is current swiper instance object", this.swiper);
    this.swiper.slideTo(1, 1000, false);
    
  },
  methods:{
      callback(){}
  }
};



